import React, { useState, useEffect } from 'react';

const Todo = (props) => {
    const [todo, setTodo] = useState([]);
    const [errorText, setErrorText] = useState([]);
    const [event, setEvent] = useState("");
    const [dueDate, setDueDate] = useState("");
    const [id, setId] = useState(0);

    useEffect(() => {
        getTodos();
    }, [])

    const getTodos = () => {
        fetch("http://127.0.0.1:5000/read")
            .then((res) => res.json())
            .then((result) => {
                if (result.data == null){
                    setErrorText(result.error)
                    setTodo([]);
                }
                else
                    setTodo(result.data)
            })
    }

    const submitForm = async (e) => {
        e.preventDefault();
        if(dueDate==""){
            alert("Fuck you")
            return;
        }
        let today = new Date()

        let data = {
            "id": id + 1
            , "event": event
            , "create_date": parseInt(today.getMonth()) + 1 + "/" + today.getDate() + "/" + today.getFullYear()
            , "due_date": dueDate
        };
        console.log(data);
        const requestOptions = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        }
        // fetch("http://127.0.0.1:5000/save",requestOptions)
        //     .then(res => res.json())
        //     .then(response => console.log(response))
        //     .then(setTimeout(getTodos(),5000));
        let response = await fetch("http://127.0.0.1:5000/save", requestOptions);
        console.log(response.json());
        getTodos();
    }

    const deleteTodo = async (id) => {
        await fetch("http://127.0.0.1:5000/delete?id=" + id);
        getTodos();
    }

    let todoList = "";
    todoList = todo.map((value, index) => {
        if (parseInt(value["id"]) > id)
            setId(parseInt(value["id"]))
            return <div id={index}>
                event: {value["event"]}, create date: {value["create_date"]}, due date: {value["due_date"]}  <button onClick={() => { deleteTodo(value["id"]) }}>X</button>
            </div>
    })
    // let todoList = [];
    // for (let key in todo) {
    //         if (parseInt(value["id"]) > id)
    //             setId(parseInt(value["id"]))
    //     console.log(todo[key])
    //     todoList.push((
    //         <div id={key}>
    //             event: {todo[key]["event"]}, create date: {todo[key]["create_date"]}, due date: {todo[key]["due_date"]}  <button onClick={() => { deleteTodo(todo[key]["id"]) }}>X</button>
    //         </div>))
    // }


    return (
        <div>
            {todoList}
            {errorText}
            <br />
            <form>
                <label style={{ marginLeft: 10 }}>event:</label> <input type="text" id="event" style={{ width: 100 }} onChange={(e) => { setEvent(e.target.value) }} />
                <label style={{ marginLeft: 10 }}>due date:</label> <input type="date" id="dueDate" style={{ width: "130px" }} onChange={(e) => { setDueDate(e.target.value) }} />
                <br />
                <button onClick={submitForm}>Add</button>
            </form>
        </div>
    );
}

export default Todo;